.usa-prose > p {
  line-height: 1.5;
  margin-bottom: 0;
  margin-top: 0;
  max-width: 68ex;
}
.usa-prose > * + p {
  margin-top: 1em;
}
.usa-prose > p + * {
  margin-top: 1em;
}

.usa-prose > h1,
.usa-prose > h2,
.usa-prose > h3,
.usa-prose > h4,
.usa-prose > h5,
.usa-prose > h6 {
  margin-bottom: 0;
  margin-top: 0;
  clear: both;
}
.usa-prose > * + h1,
.usa-prose > * + h2,
.usa-prose > * + h3,
.usa-prose > * + h4,
.usa-prose > * + h5,
.usa-prose > * + h6 {
  margin-top: 1.5em;
}
.usa-prose > h1 + *,
.usa-prose > h2 + *,
.usa-prose > h3 + *,
.usa-prose > h4 + *,
.usa-prose > h5 + *,
.usa-prose > h6 + * {
  margin-top: 1em;
}

