/*! based on uswds v3.0.1 */
/*
USAgov Variables
*/
/*
Call the var function to retrieve variables from sass map variables

ex:
var( $vars, navpage, hero, border-color );

*/
.usa-sidenav {
  color: #1a4480;
}
.usa-sidenav .active-trail {
  font-weight: bold;
}
.usa-sidenav ul > li > ul > li + li {
  border-top: none;
}
.usa-sidenav ul > li > ul > li ~ ul ~ li {
  border-top: none;
}
.usa-sidenav .usa-current {
  color: #d83933;
}
.usa-sidenav .usa-current::before {
  content: "";
  display: block;
  position: absolute;
  top: 4px;
  bottom: 4px;
  left: 0;
  width: 4px;
  background: #d83933;
  border-radius: 2px;
}
.usa-sidenav a:not(.usa-button):hover.usa-current {
  background-color: transparent;
}
.usa-sidenav a:not(.usa-button):not(.usa-current) {
  color: #1a4480;
}
.usa-sidenav a:not(.usa-button):not(.usa-current):hover {
  color: #1a4480;
  background-color: #f2f3f3;
  text-decoration: underline;
}
@media (max-width: 1024px) {
  .usa-sidenav {
    display: none;
  }
}

/*
This style overrides a style from _buttons.scss (https://github1s.com/uswds/uswds/blob/HEAD/src/stylesheets/elements/_buttons.scss#L28)
Buttons with visited links would mysteriously get white text which is not accessible contrast on our theme's primary and secondary colors.
*/
.usa-button :visited {
  color: initial;
}

/* 
This style overrides a style from drupal core (/web/core/modules/system/css/components/hidden.module.css)
The USWDS mobile footer applies a "hidden" class to closed accordions to hide the accordion content, but the drupal style hides the entire accordion.
*/
.usa-footer__primary-content--collapsible.hidden {
  display: block;
}

.usagov-hero {
  border-top: 10px solid #d83933;
  margin-bottom: 1.5rem;
  margin-top: -0.75rem;
}

.usagov-hero__callout {
  background-color: #112f4e;
  border: 1px solid #fff;
}
.usagov-hero__callout ul a,
.usagov-hero__callout ul a:hover,
.usagov-hero__callout ul a:visited {
  color: #ccecf2;
}
.usagov-hero__callout ul li {
  margin: 15px 0;
}

.usagov-cards {
  padding: 1em;
  background-color: #f3f3f3;
}

.usagov-card {
  border-radius: 0;
  box-shadow: 4px 4px 10px #aaa;
  border-color: #0081a1;
}
.usagov-card:hover {
  background-color: #ccecf2;
  border-color: #0081a1;
}

.usagov-card__heading {
  color: #1a4480;
  text-decoration-line: underline;
  text-decoration-thickness: 7px;
  text-decoration-color: #3fbde3;
  text-decoration-skip-ink: none;
  line-height: 1.8em;
}

.usagov-navpage-item {
  border: 2px solid transparent;
}
.usagov-navpage-item:hover {
  background-color: #d2ebf1;
}
.usagov-navpage-item a {
  color: #000;
  display: block;
}

.usa-alert:not(.usa-alert--info):not(.usa-alert--warning):not(.usa-alert--error):not(.usa-alert--success) {
  border-color: #dfe1e2;
  background-color: #fff;
}

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.usagov-hero-homepage {
  background-color: #112f4e;
  background-image: none;
  color: #fff;
  margin-bottom: 0;
}

.usagov-hero-feature-text a {
  text-decoration-line: underline;
  text-decoration-thickness: 7px;
  text-decoration-color: #3fbde3;
  text-decoration-skip-ink: none;
  line-height: 1.8em;
}

.usagov-hero a,
.usagov-hero a:hover,
.usagov-hero a:visited {
  color: inherit;
