  font-size: 18px;
  line-height: 1.3;
  margin-top: 20px;
}

/* Background */
.blue-back {
  background-color: #112f4e;
  grid-column: 1/3;
  grid-row: 3/8;
}

/* How do I box */
.how-box {
  background-color: #fff;
  border-radius: 3px;
  grid-column: 1/3;
  grid-row: 2/4;
  margin-left: 5vw;
  margin-right: 5vw;
  margin-top: 35px;
  max-width: 400px;
  padding: 30px 25px 20px;
}
.how-box h2,
.how-box a {
