}
.how-box a:hover {
  text-decoration: underline;
}
.how-box h2 {
  font-size: 30px;
  margin-left: auto;
  margin-right: auto;
  max-width: 960px;
}

.how-button-list {
  align-items: center;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 1fr);
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  max-width: 960px;
}

.how-button {
  align-items: center;
  border-left: 4px solid #00bde3;
  display: flex;
  height: 60px;
  padding-left: 12px;
  padding-right: 20px;
  margin-bottom: 15px;
}

/* Life Events */
.life-events {
  background-color: #112f4e;
  grid-column: 1/3;
  grid-row: 5/6;
  margin-left: 5vw;
  margin-right: 5vw;
  padding: 0;
  max-width: 100%;
}
.life-events h2 {
  font-family: "Merriweather Web", Georgia, Cambria, "Times New Roman", Times, serif;
  font-size: 35.2px;
  margin: 0;
  padding-top: 50px;
}
.life-events p {
  font-family: sans-serif;
  font-size: 17.6px;
  padding-top: 13px;
}

.life-events-carousel {
  margin: 40px 0 0;
  max-width: 100%;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.carousel {
  position: relative;
  width: 100%;
  height: 300px;
  margin: 0;
  padding: 0;
  display: flex;
}
.carousel .grid-container {
  width: 100%;
}
.carousel .usagov-cards {
  background-color: transparent;
  padding: 0;
}
.carousel .usa-card__container {
  background-color: transparent;
  box-shadow: none;
}
.carousel .usagov-card {
  border: none;
}

.previous,
.next {
  padding: 0;
  width: 40px;
  height: 40px;
  margin-top: auto;
  margin-bottom: auto;
  border: none;
  background-color: transparent;
}
.previous img,
.next img {
  width: 40px;
}
.previous img:hover,
.next img:hover {
  filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(93deg) brightness(103%) contrast(103%);
}

.carousel__navigation {
  margin: 30px 0 70px;
  list-style-type: none;
  padding-left: 0;
  align-items: center;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  width: auto;
}

.carousel__navigation_button {
  width: 44px;
  height: 44px;
  background-color: transparent;
  border: none;
}

.carousel__navigation_dot {
  fill: transparent;
  stroke: #00bde3;
  stroke-width: 6px;
}
.carousel__navigation_dot:hover {
  stroke: white;
  stroke-width: 6px;
}

.carousel__navigation_button[aria-current=true] .carousel__navigation_dot {
  fill: #fff;
  stroke: white;
  stroke-width: 6px;
}

.carousel-mover {
  width: calc(100% - 100px);
  padding-left: 11px;
  padding-right: 11px;
}

.slides {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  width: 100%;
  overflow: hidden;
}

.slide {
  height: 275px;
  flex: 0 0 100%;
  margin-top: 10px;
}
.slide a {
  color: #fff;
  text-decoration: none;
}
.slide a:visited {
  color: #fff;
  text-decoration: none;
}
.slide h3 {
  margin: 0;
  color: #00bde3;
  text-decoration: none;
  font-family: "Source Sans Pro Web", "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
  line-height: 1.25;
}
.slide p {
  margin: 0;
  font-weight: normal;
  font-family: "Source Sans Pro Web", "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
  font-size: 17.6px;
  line-height: 1.25;
  padding-top: 10px;
}

.slide a:hover > h3 {
  text-decoration: underline;
  text-decoration-color: initial;
  text-decoration-thickness: initial;
}

.slide a:hover .thumbnailBanner {
  opacity: 0.5;
}

.thumbnailDiv {
  background: #ccecf2;
  width: 100%;
  height: 85px;
  margin: auto;
  box-shadow: 0 6px 6px rgba(17, 47, 78, 0.4);
  border-style: solid;
  border-color: #fff;
}

.thumbnailBanner {
  width: 100%;
  height: 100%;
  margin: auto;
}

/* Jump */
.jump {
  padding: 0;
  text-align: center;
}
.jump a {
  color: #fff;
  font-family: sans-serif;
  font-size: 17.6px;
  text-decoration: underline;
}
.jump p {
  margin: 0 0 5px;
}

.jump1 {
  grid-column: 1/3;
  grid-row: 4/5;
  padding-top: 20px;
  background-color: #112f4e;
}

.jump2 {
  grid-column: 1/3;
  grid-row: 6/8;
  z-index: 2;
}
.jump2 a span {
  background-color: #112f4e;
}

/* All Topics Header */
.all-topics-header {
  background-color: #fff;
  grid-column: 1/3;
  grid-row: 7/9;
}
.all-topics-header h2 {
  border-bottom: 10px solid #d83933;
  color: #d83933;
  font-family: "Merriweather Web", Georgia, Cambria, "Times New Roman", Times, serif;
  font-size: 39.04px;
  font-weight: bold;
  margin: 65px 5vw 0;
  max-width: 90%;
  padding: 0;
  text-align: left;
}

.all-topics {
  background-color: #d2ebf1;
  grid-column: 1/3;
  grid-row: 9/10;
}
.all-topics p {
  color: #1b1b1b;
}

/* All Topics Cards */
.all-topics-background {
  background-color: #d2ebf1;
  padding-bottom: 100px;
  padding-top: 10px;
}

.smaller-width-div {
  margin-left: auto;
  margin-right: auto;
  max-width: 960px;
}

.homepage-card-group {
  list-style: none;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 300px));
  justify-content: center;
  grid-gap: 12px;
  row-gap: 12px;
}

.homepage-card_header {
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 12px;
}

.homepage-card {
  display: flex;
  font-family: "Source Sans Pro Web", "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
  border-width: 2px;
  border-style: solid;
  background-color: #fff;
  border-color: #707070;
  border-radius: 3px;
  box-shadow: 0 3px 6px rgba(122, 122, 122, 0.15);
  margin: 0;
  padding: 0;
}
.homepage-card:hover {
  background-color: #ccecf2;
}
.homepage-card:focus-within {
  outline: 0.25rem solid #c05600;
  outline-offset: 0;
}
.homepage-card a {
  text-decoration: none;
  height: 100%;
}
.homepage-card h2,
.homepage-card p {
  color: #1b1b1b;
  font-family: sans-serif;
  font-size: 17.6px;
  line-height: 1.3;
  text-align: center;
  padding: 0 10px;
}
.homepage-card h2 {
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
}

.homepage-card-description {
  padding-left: 10px;
  padding-right: 10px;
}

.homepage-icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 50px;
  margin-bottom: 12px;
  margin-top: 25px;
}

/* Tablet & Desktop */
@media (min-width: 480px) {
  .welcome-box {
    grid-column: 2/3;
    padding: 30px 40px 35px 30px;
  }
  .how-box {
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;
    max-width: 1024px;
    padding-top: 18px;
  }
  .how-button-list {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    margin-top: 20px;
  }
  .how-button {
    margin-bottom: auto;
  }
  .life-events h2 {
    padding-top: 30px;
  }
  .carousel-mover {
    max-width: 100%;
    padding-left: 0;
    padding-right: 0;
  }
  .carousel .usa-card-group {
    margin-left: 0;
    margin-right: 0;
  }
  .slide {
    flex: 0 0 50%;
    padding-right: 30px;
    padding-left: 30px;
  }
  .slide:not(:last-child) {
    margin-right: 0;
  }
  .all-topics-header h2 {
    margin-left: auto;
    margin-right: auto;
    max-width: 1024px;
    padding: 0 0 5px 75px;
  }
  .homepage-card_header {
    padding-left: 0;
    padding-right: 0;
  }
  .homepage-card-group {
    grid-template-columns: repeat(auto-fit, minmax(100px, 224px));
  }
}
/*Tablet */
@media (max-width: 700px) and (min-width: 480px) {
  .how-button-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 30px;
  }
  .how-button {
    margin-bottom: 15px;
  }
}
/* Desktop */
@media (min-width: 1024px) {
  .life-events {
    width: 1024px;
    margin-left: auto;
    margin-right: auto;
    max-width: 1024px;
    padding: 0;
  }
  .life-events-intro {
    margin-left: 0;
    margin-right: auto;
    max-width: 960px;
    padding: 0 0 0 75px;
  }
  .life-events-carousel {
    width: 1024px;
  }
  .carousel-mover {
    width: 100%;
    max-width: 940px;
    margin-left: 0;
    margin-right: auto;
  }
  .carousel .usa-card-group {
    margin-left: 0;
    margin-right: 0;
  }
  .slide {
    flex: 0 0 33.3333333333%;
    padding-right: 30px;
  }
  .slide:not(:last-child) {
    margin-right: 0;
  }
}
