  border-width: 3px;
  border-style: solid;
}

.usa-main-border-error {
  border-left: 3px solid #d83933;
}
.usa-main-border-error .usa-border-error {
  border-left: none;
}

.usa-border-error {
  border-left: 3px solid #d83933;
}

.usa-form-spacing {
  padding-left: 1rem;
}

#myform div.margin-x-auto {
  margin-left: 3rem;
}

#myform div.maxw-mobile {
  max-width: 26rem;
}

/**
#myform.usa-form .usa-input, #myform .usa-combo-box__input {
  max-width: 20rem;
}
*/
.usa-error--alert {
  display: none;
}

.currentMenuItem {
  position: relative;
}

.currentMenuItem::after {
  background-color: #00bde3;
  border-radius: 0;
  content: "";
  display: block;
  position: absolute;
  height: 0.25rem;
  left: 1rem;
  right: 1rem;
  bottom: 0;
}

.block-wizardenglish {
  background-color: #f3f3f3;
  border-top: 4px solid #112f4e;
  padding-top: 2rem;
  padding-left: 3rem;
  padding-right: 2rem;
}
@media (max-width: 64em) {
  .block-wizardenglish {
    padding-left: 2rem;
  }
}

@media (min-width: 64em) {
  .block-wizardenglish {
    margin-left: 5rem;
    max-width: 80%;
  }
}
.block-wizardenglish div.usa-prose h1 {
  color: #112f4e;
  padding-bottom: 1.5rem;
  margin-top: 0;
  font-size: 2.25rem;
}
@media (max-width: 40em) {
  .block-wizardenglish div.usa-prose h1 {
    font-size: 2rem;
  }
}

.block-wizardenglish div.usa-prose h1.usa-wizard-title {
  padding-bottom: 0;
}

.block-wizardenglish div.usa-prose h1 .usa-pros {
  padding-bottom: 1rem;
}

.block-wizardenglish .usa-prose {
  font-size: 1.2rem;
}

.block-wizardenglish .usa-button {
  color: #1b1b1b;
  background-color: #ccecf2;
}
@media (max-width: 40em) {
  .block-wizardenglish .usa-button {
    margin-bottom: 8px;
  }
}

.block-wizardenglish .usa-prose > p.usa-scam-label {
  max-width: 110ex;
  padding-bottom: 0.2rem;
  margin-bottom: 1rem;
}

.block-wizardenglish .usa-scam-label {
  border-bottom: 1px solid gray;
  color: #112f43;
  font-weight: 500;
}

button#prior.usa-button--outline {
  background-color: #fff;
  box-shadow: inset 0 0 0 2px #000;
}

button#next.usa-button, .block-wizardenglish a.usa-button {
  background-color: #ccecf2;
  box-shadow: inset 0 0 0 2px #0081a1;
}

.block-wizardenglish .usa-prose > p {
  max-width: 110ex;
}

.block-wizardenglish .usa-radio__input--tile:checked + [class*=__label] {
  background-color: #ccecf2;
}

.wizard_error {
  border-left: 3px solid #d83933;
  padding-left: 5px;
}

.block-wizardenglish div.usa-error {
  padding-left: 5px;
  padding-right: 5px;
}

.block-wizardenglish .usa-radio__input--tile:checked + .usa-radio__label {
  border-color: #c05600;
}

.block-wizardenglish [tabindex]:focus {
  outline: 0.25rem solid #c05600;
  outline-offset: 0;
  width: fit-content;
  margin-left: 0;
}

@media (max-width: 40em) {
  .usa-reverse {
    flex-direction: column-reverse;
  }
}
@media only screen and (max-width: 500px) {
  .usa-identifier {
    position: absolute;
    z-index: 2000000005;
  }
}
#backToTop {
  z-index: 2000000003;
}

#pagesurvey-trgt-qualtrics {
  background: white;
  max-width: 960px;
  display: inline-block;
  margin: 0 auto 0 auto;
}

div[class^=QSIUserDefinedHTML] {
  z-index: 1;
}

#pagesurvey-qual-hdr {
  background: #CCECF2;
  font-size: 130%;
  display: block;
  color: #1B1B1B;
  padding: 12px 0 15px 0px;
  margin: 0 auto 0 auto;
  max-width: 960px;
  height: auto;
  text-align: center;
  font-family: Merriweather Web, Georgia, Cambria, Times New Roman, Times, serif;
  font-weight: bold;
}

.scrollable {
  overflow: unset !important;
}

#pagesurvey-qual-container:focus {
  outline: #C05600 solid 3px;
}

@media only screen and (max-width: 500px) {
  div[class^=QSIUserDefinedHTML] {
    left: -16px;
  }
}
